import Input from '@mui/material/Input';
import { Button } from '@mui/material';
import "./Input.css";

const Inputer = ({input, setInput,findWeather}) => {

    const handleChange =(event) =>{
        setInput(event.target.value);
    };
     
    const handleClick =(event) =>{
        event.preventDefault();
        findWeather();
    };
  return (
    <div className="inputBox">
        <div className="inputBox__field">
            <h1>Soban Weather App</h1>
            <p>Powered by</p>
             <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/1200px-Google_%22G%22_Logo.svg.png" alt="logo" />
            <form>
            <Input placeholder="Enter the city name" onChange={handleChange} value={input}/><br/>
            <Button variant="contained" color="primary" type="submit" onClick={handleClick}>Search</Button>   
            </form>  
            </div>
    </div>
  );
};

export default Inputer;
